<template>
  <div class="row">
    <div class="col">

      <ul class="stores">
        <draggable v-model="stores" @start="drag=true" @end="drag=false">
        <store v-for="(item, index) in stores"
               :key="index"
               :store-item="item"
               @remove-store="remove"
               @save-changes="setStores"
               />
        </draggable>
      </ul>

      <form class="add-store" v-show="showForm">
        <div class="form-row">
          <div class="form-group col-lg-4">
            <label for="inputName">Store Name</label>
            <input type="text" class="form-control" id="inputName" placeholder="Name"
                   :class="[ errorName ? 'is-invalid' : 'is-valid' ]"
                   v-model="newStore.name">
          </div>
          <div class="form-group col-lg-6">
            <label for="inputAddress">Address</label>
            <input type="text" class="form-control" id="inputAddress" placeholder="Address"
                   :class="[ errorAddress ? 'is-invalid' : 'is-valid' ]"
                   v-model="newStore.address">
          </div>
          <div class="form-group col-lg-2">
            <label for="inputTime">Working hours</label>
            <input type="text" class="form-control" id="inputTime" placeholder="Working hours"
                   :class="[ errorTime ? 'is-invalid' : 'is-valid' ]"
                   v-model="newStore.time">
          </div>
        </div>
        <div class="btn-add-store" @click="addStore"> add store
        </div>
        <i class="fa fa-times close-form" aria-hidden="true" @click="showForm = false"></i>
      </form>
      <div class="btn-add-store" v-show="!showForm" @click="showForm = !showForm">
        add store
      </div>
    </div>
  </div>
</template>


<script>
  import Store from './Store'
  import draggable from 'vuedraggable'

  export default {
    name: 'Stores',
    components: {
      Store,
      draggable
    },
    data () {
      return {
        stores: [],
        id: '',
        showForm: false,
        newStore: {
          id: '',
          name: '',
          address: '',
          time: '',
          goods: []
        }
      }
    },
    watch: {
      stores () {
        for (let i = 0; i < this.stores.length; ++i) {
          let count = i;
          count += 1;
          this.stores[i].id = count;
          this.id = count;
          this.setStores()
        }
      }
    },
    computed: {
      requiredFields () {
        return !!(this.newStore.name && this.newStore.address && this.newStore.time)
      },
      errorName () {
        return !this.newStore.name
      },
      errorAddress () {
        return !this.newStore.address
      },
      errorTime () {
        return !this.newStore.time
      }
    },
    methods: {
      getStores () {
        this.stores = JSON.parse(localStorage.getItem('stores')) || []
      },
      setStores () {
        localStorage.setItem('stores', JSON.stringify(this.stores))
      },
      addStore () {
        if (this.showForm && this.requiredFields) {
          this.newStore.id = this.id += 1;
          this.stores.push(this.newStore);
          this.newStore = {
            id: '',
            name: '',
            address: '',
            time: '',
            goods: []
          };
          this.showForm = !this.showForm;
          this.setStores();
        }
      },
      remove (id) {
        for (let i = 0; i < this.stores.length; i += 1) {
          if (this.stores[i].id === id) {
            this.stores.splice(i, 1);
            this.setStores()
          }
        }
      }
    },
    mounted () {
      this.getStores();
      this.stores.length > 0 ? this.id = this.stores[this.stores.length - 1].id : this.id = 0;
    }
  }
</script>


<style lang="scss">

  .stores {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .add-store {
    padding: 40px 0;
    position: relative;
  }

  .btn-add-store {
    background-color: #40c0ff;
    border: solid 1px #37acff;
    border-radius: 5px;
    height: 45px;
    line-height: 45px;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
    transition: all ease .2s;
    margin-bottom: 20px;
    &:hover {
      background-color: #3dd1ff;
      border-color: #40c0ff;
    }
  }
  .close-form {
    position: absolute;
    top: 10px;
    right: 0;
    color: #40c0ff;
    cursor: pointer;
    font-size: 1.3em;
  }
</style>



