<template>
  <div class="row">
    <div class="col-12">
      <div class="store-header">
        <div class="store-header-id">{{store.id}}</div>
        <h3 class="store-header-name">{{store.name}}</h3>
        <h5 class="store-header-address"><i class="fa fa-map-marker" aria-hidden="true"></i> {{store.address}}</h5>
        <h5 class="store-header-time"><i class="fa fa-clock-o" aria-hidden="true"></i> {{store.time}}</h5>
        <i class="fa fa-plus-square-o btn-add-goods" aria-hidden="true" @click="showForm = !showForm"></i>
      </div>
    </div>

    <div class="col-12">
      <form class="add-goods" v-show="showForm">
        <div class="form-row">
          <div class="form-group col-lg-5">
            <label for="inputNameGoods">Name</label>
            <input type="text" class="form-control" id="inputNameGoods" placeholder="Name"
                   :class="[ errorName ? 'is-invalid' : 'is-valid' ]"
                   v-model="newGoods.name">
          </div>
          <div class="form-group col-lg-7">
            <label for="inputDescription">Description</label>
            <textarea class="form-control" id="inputDescription" placeholder="Description"
                      :class="[ errorDescription ? 'is-invalid' : 'is-valid' ]"
                      v-model="newGoods.description"></textarea>
          </div>

        </div>
        <div class="btn-add-new-goods" @click="addGoods">add goods</div>
        <i class="fa fa-times close-form" aria-hidden="true" @click="showForm = false"></i>
      </form>
    </div>

    <div class="col-12">
      <table class="table table-responsive-xl goods-table">
        <thead>
        <tr>
          <th>Name</th>
          <th>Description</th>
        </tr>
        </thead>
        <tbody>
        <goods-item
          v-for="item in store.goods"
          :item="item"
          @save-goods="saveGoods"/>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import GoodsItem from './GoodsItem'

  export default {
    name: 'StoreView',
    components: {
      GoodsItem
    },
    props: ['storeData'],
    computed: {
      errorName() {
        return !this.newGoods.name
      },
      errorDescription() {
        return !this.newGoods.description
      }
    },
    data() {
      return {
        storeId: +this.$route.params.id,
        stores: [],
        store: {},
        showForm: false,
        newGoods: {
          name: '',
          description: ''
        }
      }
    },
    methods: {
      fetchData() {
        this.stores = JSON.parse(localStorage.getItem('stores'))
        for (let i = 0; i < this.stores.length; i += 1) {
          if (this.storeId === this.stores[i].id) {
            this.store = this.stores[i]
          }
        }
      },
      addGoods() {
        if (!this.errorName && !this.errorDescription) {
          this.store.goods.push(this.newGoods);
          localStorage.setItem('stores', JSON.stringify(this.stores));
          this.newGoods = {
            name: '',
            description: ''
          }
        }
      },
      saveGoods() {
        localStorage.setItem('stores', JSON.stringify(this.stores));
      }
    },
    created() {
      this.fetchData()
    }
  }
</script>

<style lang="scss">

  .store-header {
    border: solid 1px #e0e0e0;
    border-radius: 5px;
    background-color: #fff;
    margin-bottom: 20px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
    &-id {
      position: absolute;
      top: 5px;
      left: 25px;
      font-size: .7em;
      color: rgba(0, 0, 0, 0.5);
    }
    &-name {
      margin-top: 10px;
      font-size: 2em;
      font-weight: bold;
    }
    &-address {
      color: rgba(0, 0, 0, 0.5);
    }
    &-time {
      color: rgba(0, 0, 0, 0.5);
      margin-bottom: 15px;
    }
  }

  .goods-table {
    background-color: #fff;

  }

  .btn-add-goods {
    position: absolute;
    left: 30px;
    bottom: 30px;
    font-size: 2em;
    color: #40c0ff;
    cursor: pointer;
    transition: all ease .2s;
    &:hover {
      color: #3fe7ff;

    }
  }

  .add-goods {
    margin: 10px 0;
    padding: 25px 20px;
    background-color: #fff;
    border: solid 1px #e0e0e0;
    border-radius: 5px;
    position: relative;
  }

  .btn-add-new-goods {
    background-color: #40c0ff;
    border: solid 1px #37acff;
    border-radius: 5px;
    height: 40px;
    line-height: 40px;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
    transition: all ease .2s;
    &:hover {
      background-color: #3dd1ff;
      border-color: #40c0ff;
    }
  }

  .close-form {
    position: absolute;
    top: 10px;
    right: 10px;
    color: #40c0ff;
    cursor: pointer;
    font-size: 1.3em;
  }

</style>



