<template>
  <li class="store">
    <div class="container">
      <div class="row">
        <div class="store-id">
          {{storeItem.id}}
        </div>
        <div class="store-edit">
          <i class="fa fa-pencil" v-if="!edit" @click="edit = true"></i>
          <i class="fa fa-check" v-else="edit" @click="saveChanges"></i>
        </div>

        <div class="col-lg-4 col-md-3 store-name" v-if="!edit">
          {{storeItem.name}}
        </div>
        <div class="col-lg-4 col-md-3 store-name" v-if="edit">
          <input type="text" class="form-control"
                 :class="[ errorName ? 'is-invalid' : 'is-valid' ]"
                 v-model="storeItem.name">
        </div>

        <div class="col-lg-5  col-md-3 store-address"  v-if="!edit">
          <i class="fa fa-map-marker" aria-hidden="true"></i> {{storeItem.address}}
        </div>
        <div class="col-lg-5  col-md-3 store-address"  v-if="edit">
          <input type="text" class="form-control"
                 :class="[ errorAddress ? 'is-invalid' : 'is-valid' ]"
                 v-model="storeItem.address">
        </div>

        <div class="col-lg-2 col-md-3 store-time"  v-if="!edit">
          <i class="fa fa-clock-o" aria-hidden="true"></i> {{storeItem.time}}
        </div>
        <div class="col-lg-2 col-md-3 store-time"  v-if="edit">
          <input type="text" class="form-control"
                 :class="[ errorTime ? 'is-invalid' : 'is-valid' ]"
                 v-model="storeItem.time">
        </div>

        <div class="col-lg-1 col-md-2 store-btn" v-show="!edit">
          <router-link :to="'store/'+storeItem.id">goods</router-link>
        </div>
        <i class="fa fa-times remove-store" v-if="edit" @click="removeStore"></i>


      </div>
    </div>

  </li>

</template>


<script>
  export default {
    name: 'Store',
    props: ['storeItem'],
    data () {
      return {
        edit: false
      }
    },
    computed: {
      errorName () {
        return !this.storeItem.name
      },
      errorAddress () {
        return !this.storeItem.address
      },
      errorTime () {
        return !this.storeItem.time
      }
    },
    methods: {
      removeStore () {
        this.$emit('remove-store', this.storeItem.id);
        this.edit = false
      },
      saveChanges () {

        let bool;
        bool = !!(this.storeItem.name && this.storeItem.address && this.storeItem.time);
        if (bool) {
          this.$emit('save-changes')
          this.edit = false
        }
      }
    }
  }
</script>


<style lang="scss">

  .store {
    line-height: 2em;
    border: 1px solid #e0e0e0;
    margin: 5px 0;
    border-radius: 5px;
    position: relative;
    background-color: #ffffff;
    text-align: left;
    padding: 10px 10px;
    transition: all ease .2s;
    cursor: move;
    &:hover {
      background-color: rgba(0, 255, 90, 0.11);
    }
    .store {

      &-id {
        position: absolute;
        left: 7px;
        top: 5px;
        font-size: .7em;
        color: rgba(145, 145, 145, 0.55);
        text-decoration: none;

      }
      &-name {
        padding-left: 50px;
        font-size: 1.2em;
        font-weight: bold;
      }
      &-address {
        font-style: italic;
        color: #6c6c6c;
      }
      &-time {
        color: #6c6c6c;
      }
      &-btn {
        padding: 0;
      }
      &-edit {
        cursor: pointer;
        position: absolute;
        left: 30px;
        z-index: 10;
      }
    }
    a {
      text-align: center;
      line-height: 1.9em;
      border: 1px solid #ffd720;
      background-color: #ffe823;
      border-radius: 5px;
      text-decoration: none;
      color: #757575;
      font-weight: bold;
      display: block;
      width: 100%;
      transition: all ease .2s;
      &:hover {
        background-color: #fffb25;
        border: 1px solid #ffe823;
      }
    }
  }

  .fa {
    cursor: pointer;
  }
  input.form-control {
    width: 100%;
    padding: 5px;
  }
  .remove-store {
    font-size: 1.5em;
    position: absolute;
    right: 10px;
    line-height: 1.3em;
    color: red;
    z-index: 20;
    transition: all ease .2s;
    &:hover {
      color: #ffffff;
    }
  }

  @media (max-width: 767px) {
    .store-name, .store-address, .store-time {
      text-align: center;
    }
    input.form-control {
      width: 100%;
      margin: 10px 0;
    }
    .remove-store {
      top: -5px;
      right: 2px;
    }
  }


</style>



