import Vue from 'vue'
import Router from 'vue-router'
import Stores from '@/components/Stores'
import StoreView from '@/components/StoreView'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/stores',
      name: 'Stores',
      component: Stores
    },
    {
      path: '/store/:id',
      name: 'StoreRout',
      component: StoreView
    }
  ]
})



