<template>
<tr class="goods-item">

  <td class="goods-name">
    <i class="fa fa-pencil edit-goods" v-if="!edit" @click="edit = true"></i>
    <i class="fa fa-check save-change"  v-if="edit" @click="saveChange"></i>

    <div v-if="!edit">
      <h5 class="goods-name">{{item.name}}</h5>
    </div>
    <div v-if="edit">
      <input type="text" class="form-control form-edit-goods"
             :class="[ errorName ? 'is-invalid' : 'is-valid' ]"
             v-model="item.name">
    </div>
  </td>

  <td class="goods-description">
    <div v-if="!edit">
      <p class="goods-description">{{item.description}}</p>
    </div>
    <div v-if="edit">
        <textarea type="text" class="form-control form-edit-goods"
                  :class="[ errorDescription ? 'is-invalid' : 'is-valid' ]"
                  v-model="item.description">
        </textarea>
    </div>
  </td>
</tr>
</template>


<script>
  export default {
    name: 'GoodsItem',
    props: ['item'],
    computed: {
      errorName () {
        return !this.item.name
      },
      errorDescription () {
        return !this.item.description
      }
    },
    data () {
      return {
        edit: false
      }
    },
    methods: {
      saveChange () {
        if (!this.errorName && !this.errorDescription) {
          this.$emit('save-goods');
          this.edit = false
        }
      }
    }
  }
</script>


<style lang="scss">
  .goods-item {

  }

  .goods-name {
    margin-left: 35px;
    margin-bottom: 0;
    text-align: left;
    font-size: 1.1em;
    position: relative;
  }

  .goods-description {
    margin: 0;
    text-align: left;
    font-size: 1em;
    position: relative;
  }
  .edit-goods, .save-change {
    position: absolute;
    top: 15px;
    left: 20px;
    margin: 0;
    padding: 0;
    cursor: pointer;
    z-index: 10;
    transition: all ease .2s;
    color: #40c0ff;
    &:hover {
      color: #2578ff;
    }
  }

  input.form-edit-goods {
    width: calc(100% - 35px);
    padding: 6px;
    margin: 0 0 0 35px;
  }
  textarea.form-edit-goods {
    width: 100%;
  }
</style>



